<template>
  <div class="Withdraw" v-loading="load">

    <ChooseCoin type="withdraw" v-on:setCoin="setCoin" />

    <div class="space20"></div>


    <div class="main" v-if="currCoin">

      <h1 style="font-size: 18px;" class="font-bold">{{currCoin.coin.coinName}}&nbsp;提现</h1>

      <div class="space30"></div>

      <div class="name">提现额度&nbsp;&nbsp;(限制额度 {{currCoin.coin.minWithdraw}}-{{currCoin.coin.maxWithdraw}} {{currCoin.coin.coinName}})</div>
      <div class="inputGroup flex align">
        <input type="text" :placeholder="`请输入提现额度 ( 可用${currCoin.balance.amount})`" name="" id=""
          v-model="withrawInfo.amount">
        <div class="allIn" @click="withrawInfo.amount = currCoin.balance.amount">全部</div>
      </div>
      <!-- inputGroup -->

      <div class="space20"></div>

      <div class="name">提现地址</div>
      <div class="inputGroup flex align between">
        <input type="text" placeholder="请输入提现地址" v-model="withrawInfo.address">
      </div>
      <!-- inputGroup -->

      <div v-if="googleStatue=='success'">

        <div class="space20"></div>
        <div class="name">谷歌验证码</div>
        <div class="inputGroup flex align between">
          <input type="text" placeholder="请输入谷歌验证码" v-model="withrawInfo.google">
        </div>
        <!-- inputGroup -->

      </div>
      <!-- end if -->

      

      <div class="space20"></div>

      <div class="name">提现验证码</div>
      <div class="inputGroup flex align between">
        <input type="text" placeholder="请输入提现验证码" v-model="withrawInfo.code">
        <GetCode :codeData="{number:userNumber,type:'withdraw'}" />
      </div>
      <!-- inputGroup -->


      <div class="space20"></div>

      <div class="themBtn"  @click="submitWithdraw">提&nbsp;&nbsp;现</div>

    </div>

    <div class="space50"></div>


    <div class="info">
      <div class="red"><i class="el-icon-warning"></i>&nbsp;&nbsp;<span>提现操作等待区块确认需1-3个工作日，请耐心等候！</span></div>
    </div>
    <!-- info -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  import ChooseCoin from './chooseCoin'
  import GetCode from '@com/common/getCode'

  import {
    withdraw,
    googleCheck
  } from '../../data/apis';

  export default {
    props: ['dataGroup'],
    data() {
      return {
        currCoin: "",
        withrawInfo: {
          address: '',
          amount: '',
          code: 110112,
          coinId: '',
          google:''
        },

        load:true,
        googleStatue:false//谷歌验证是否开启
      }
    },
    components: {
      ChooseCoin,
      GetCode
    },
    computed: {
      ...mapState(['userInfo']),
      userNumber(){
        return this.userInfo.user.area+this.userInfo.user.registerAccount
      }
    },
    methods: {
      async getStatue() { //获取谷歌状态
        let res = await googleCheck();
        this.googleStatue = res.data;
      }, //getStatue
      setCoin(item) { //当前币种选择
        console.log(item)
        this.currCoin = item;
        this.withrawInfo.coinId = item.coin.id;
        this.load = false;
      }, //setCoin
      submitWithdraw() { //申请提现 

        if (!this.withrawInfo.amount) {
          this.$message.error('请输入提现金额');
          return;
        }

        if (!this.withrawInfo.address) {
          this.$message.error('请输入提现地址');
          return;
        }

        if (this.googleStatue=='success' && !this.withrawInfo.google) {
          this.$message.error('请输入谷歌验证码');
          return;
        }

        if (!this.withrawInfo.code) {
          this.$message.error('请输入提现验证码');
          return;
        }
        this.load = true;
        withdraw(this.withrawInfo).then(v => {
          this.$message.success(v.message);
          this.currCoin.balance.amount -= this.withrawInfo.amount;
          this.withrawInfo = {
            address: null,
            amount: null,
            code: null
          }
          this.load = false;
        }).catch(e=>{
          this.load = false;
        })
      } //submitWithdraw
    },
    mounted() {
      this.getStatue();
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";

  .Withdraw {
    padding: 30px 0;

    .info {
      .red {
        color: #F15772;
        margin-bottom: 15px;
      }

      color: $them_color_gray_light;
      line-height: 1.8em;
    }

    .name {
      font-size: 14px;
    }

    .inputGroup {
      padding: 0 20px;
      // background: #f6f6f6;
      border: 1px solid #E9E9E9;
      margin: 10px 0;
      border-radius: 2px;

      input {
        flex: 1;
        border: none;
        background: none;
        padding: 20px;
        font-size: 18px;
      }

      .allIn {
        color: $them_color;
        font-size: 18px;
        text-decoration: underline;
        cursor: pointer;
      }
    }

  }

</style>



