<template>
  <div class="chooseCoin">

    <h1 class="font-bold">选择充值币种</h1>

    <div class="selectBox flex align wrap">

      <div class="item flex align" v-for="(item,index) in listArr" @click="setCoin(item)" :key="index" :class="{active:currCoin.coin.coinName==item.coin.coinName}">
        <span>{{item.coin.coinName}}</span>
      </div>

    </div>
    <!-- selectBox -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  import {
    rechargeAddress
  } from '../../data/apis';

  export default {
    props:['type'],
    props: [],
    data() {
      return {
        listArr:[],//币种列表
        currCoin:null//当前币种
      }
    },
    computed: {
      ...mapState(['userInfo']),
    },
    methods: {
        filterType(data){//筛选不可充提币种
          let resdata; 
          if(this.type=='recharge'){
            resdata = data.filter(x=>Boolean(x.coin.closeRecharge=='N'));
          }else{
            resdata = data.filter(x=>Boolean(x.coin.closeWithdraw=='N'));
          }
          return resdata;
      },//filterType
      getInfo(){//获取数据
        rechargeAddress().then(v=>{
          console.log(v)
          this.listArr = this.filterType(v.data);
          this.currCoin = this.listArr[0];
          console.log('ccc=>',this.currCoin)
          this.$emit('setCoin',this.currCoin)
        })
      },//getInfo
      setCoin(item){//选择币种
        this.currCoin = item;
        this.$emit('setCoin',item)
      }//chooseCoin
    },
    mounted() {
      this.getInfo();
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";
  .chooseCoin{
    h1{
      font-size: 18px;
      margin-bottom: 20px;
      // color: $them_color_gray_light;
    }
    .selectBox{
      .item{
        border: 1px solid #D4D4D4;
        cursor: pointer;
        span{
            color: $them_color_gray;
            font-weight: 500;
            font-size: 18px;
        }
        &.active{
           border: 1px solid $them_color;
           background: $them_color;
          span{
             color: white;
          }
        }
        margin-right: 15px;
        margin-bottom: 15px;
        padding: 5px 30px;
        border-radius: 2px;

        i{
          margin-right: 10px;
        }
      }
    }
  }
</style>



