<template>
  <div class="charge" v-loading="load">


    <ChooseCoin  type="recharge" v-on:setCoin="setCoin"/>

    <div class="mainBox flex align">

      <div class="codeBox">
        <div class="qrcode" ref="qrCodeUrl" id="qrCodeUrl"></div>
      </div>

      <div class="text"  v-if="currCoin">
        <h2>{{currCoin.coin.coinName}}充值地址</h2>
        <div class="trx font-bold">{{copyText}}</div>
        <div class="copyBtn copycode"  :data-clipboard-text="copyText" @click="copy">复制地址</div>
      </div>
      <!-- text -->

    </div>
    <!-- mainBox -->

    <div class="info">
      <div class="red"><i class="el-icon-warning"></i>&nbsp;&nbsp;<span>一旦存入，任何网络钱包提款将在托管中保留7天，以确保安全。</span></div>
      <div>请勿将任何非BTC（ERC20）资产充值至上述地址，否则将不会检索该资产。 充值至上述地址后，需要确认整个网络节点。 经过12次网络确认后，该帐户将被记入帐户。 经过12个网络确认后，您可以提取资金。 您的充值地址不会经常更改，可以重复充值。 如果您进行更改，我们将尝试通过网站公告或电子邮件通知您。 确保您的计算机和浏览器是安全的，以防止信息被篡改或泄漏。</div>
    </div>
    <!-- info -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  import QRCode from 'qrcodejs2'
  import Clipboard from 'clipboard';

  import ChooseCoin from './chooseCoin'

  

  export default {
    props: ['dataGroup'],
    data() {
      return {
         copyText:'',
         currCoin:'',
         load:true
      }
    },
    components: {
        ChooseCoin
    },
    computed: {
      ...mapState(['userInfo']),
    },
    methods: {
      setCoin(item){//当前币种选择
        this.qrcode(item.rechargeAddress.address);
        this.copyText = item.rechargeAddress.address;
        this.currCoin = item;
        this.load = false;
      },//setCoin
      async qrcode(address) { //生成二维码
        this.$refs.qrCodeUrl.innerHTML="";//先移除
        var qrcode = new QRCode(this.$refs.qrCodeUrl, {
          text: address,
          colorDark : "#2b3b4b",
          width: 150,
          height: 150,
          correctLevel: QRCode.CorrectLevel.H
        })
      }, //qrcode

      copy() { //复制
        var clipboard = new Clipboard('.copycode')
        clipboard.on('success', e => {
          this.$message.success(this.$t('wallet.common.toast_copy_ok'));
          clipboard.destroy()
        })
        clipboard.on('error', e => {
          this.$message.error(this.$t('wallet.common.toast_copy_fail'));
          clipboard.destroy()
        })
      }//copy

    },
    mounted() {

    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../../styles/Val";
  .charge{
    padding: 30px 0;
    .mainBox {
      padding: 50px 0;
      .codeBox{
        margin-right: 60px;
      }
      h2{
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 30px;
      }
      .trx{
        font-size: 18px;
        color: $them_color;
      }
      .copyBtn{
        border: 1px solid $them_color;
        color: $them_color;
        display: inline-block;
        padding: 5px 20px;
        border-radius: 2px;
        margin-top: 10px;
        cursor: pointer;
        transition: all 0.3s;
        -webkit-transition: all 0.3s;
        &:hover{
          background: $them_color;
          color: white;
        }
      }
    }
    .info{
      .red{
        color: #F15772;
        margin-bottom: 15px;
      }
      color: $them_color_gray_light;
      line-height: 1.8em;
    }
  }
</style>



