<template>
  <div class="userAssets">

    <div class="menmuTitle flex align between">
      <h1>代币充提</h1>
      <div class="goLink pointer hoverTr3" @click="gopage('/userCenter/userWithdrawList')">提现记录&nbsp;&nbsp;<i
          class="el-icon-right"></i></div>
    </div>
    <!-- menmuTitle -->

    <div class="mainBox">

      <div class="space30"></div>

      <el-tabs v-model="activeName">
        <el-tab-pane name="Charge">
          <span slot="label" class="tabTitle"><i class="el-icon-money"></i> 充值</span>
        </el-tab-pane>
        <el-tab-pane name="Withdraw">
          <span slot="label" class="tabTitle"><i class="el-icon-position"></i> 提现</span>
        </el-tab-pane>
      </el-tabs>


      <Withdraw v-if="activeName=='Withdraw'" />
      <Charge v-if="activeName=='Charge'" />

    </div>
    <!-- mainBox -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import Charge from '@com/inout/charge'
  import Withdraw from '@com/inout/withdraw'

  export default {
    data() {
      return {
        activeName: 'Charge'
      }
    },
    components: {
      Charge,
      Withdraw
    },
    computed: {
      ...mapState(['userInfo'])
    },
    methods: {

    },
    mounted() {

    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .tabTitle {
    font-size: 18px;
    font-weight: bold;
    padding: 0 10px;
    padding-right: 20px;

    i {
      margin-right: 10px;
      font-size: 23px;
    }
  }

  .is-active {
    * {
      color: $them_color;
    }
  }

</style>



